
  import ChinaAddressData from 'vux/src/datas/china_address.json'
import Search from 'vux/src/components/search/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Card from 'vux/src/components/card/index.vue'
import value2name from 'vux/src/filters/value2name.js'


  export default {
      components: {
          Search,
          Scroller,
          Divider,
          Spinner,
          Group,
          Cell,
          Card,

      },
      mounted(){
      console.log(APIURL);
      this.fetchData();

      },
      methods: {
        getName (value) {
  return value2name(value, ChinaAddressData)
},
          load3 () {
              setTimeout(() => {
                  setTimeout(() => {
                      this.demo3Value.pullupStatus = 'default'
                  }, 100)
                  this.page++
                  this.fetchData();
                  console.log(this.page)
                  if (this.pullupStatus==1) { // unload plugin
                      setTimeout(() => {
                          this.demo3Value.pullupStatus = 'disabled'
                      }, 100)
                  }
              }, 100)
          },
          fetchData(){
              var resource = this.$resource(APIURL+'orderPassanger/getUsersByOrderId', {
                  //                    include: ''
              });
              // get item
              resource.get({
                  order_id:this.$route.params.id,
                  page:this.page,
              }).then((response) => {
                  if(response.data.errcode==0){
                      this.item = response.data.data;
                      console.log(this.page*this.pageSize);
                      console.log(this.item.length);
                      if(this.page*this.pageSize>this.item.length){
                          this.pullupStatus=1
                      }
                      console.log(this.item);
                  }else {
                      console.log('出小差了');
                  }

              });
          },
          resultClick (item1) {
              window.alert('you click the result item: ' + JSON.stringify(item1))
          },
          getResult (val) {
              this.results = val ? getResult(this.value) : []
          }
  },
  data () {
      return {
          n3: 10,
          demo3Value: {
              pullupStatus: ''
          },
          pullupConfig2: {
              content: '上拉加载更多',
              downContent: '松开进行加载',
              upContent: '上拉加载更多',
              loadingContent: '加载中...'
          },
          item: '',
          page: 1,
          pageSize:10,
          pullupStatus:0,
          results: [],
          value: 'test'
      }
  }
  }
  function getResult (val) {
      let rs = []
      for (let i = 0; i < 8; i++) {
          rs.push({
              title: `${val} result: ${i + 1} `,
              other: i
          })
      }
      return rs
  }



