
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'


export default {
  components: {
    Group,
    Cell
  },
  data(){
      return {
//                msg: 'hello vue',
          item: '',
          out_status: false,
          status: true,
          cz_status: false,
      }
  },
    mounted(){
    console.log('Component.');
    this.getCookie();
    },
  methods: {
    onClick () {
      console.log('on click')
    },
    fetchData(){
        var resource = this.$resource(APIURL+'users/me', {
//                    include: ''
        });
        resource.get({}).then((response) => {
            console.log(response);
            if(response.data.errcode==0){
                //console.log(response);
                this.item = response.data.data;
                if(this.item.type==1){
                  this.cz_status = true;
                  this.item.sf=1
                }else {
                  this.item.sf=0
                }
                this.out_status = true;
                this.status = false;
            }else {
                console.log('出小差了');
            }
        });

    },
      getCookie(){
          var laravel_token=cookie.get('laravel_token')
          if(laravel_token){
              this.fetchData()
          }else{
              console.log('您还未登录!');
          }
      },
      logout() {
          this.$http.post(APIURL+'users/logout', {
          }).then(response => {
              if (response.data.errcode==0) {
              cookie.remove('laravel_token')
              this.$vux.alert.show({
                  title: '退出成功!',
                  content: '',
                  onShow () {
                  console.log('Plugin: I\'m showing')
              },
              onHide () {
                  console.log('Plugin: I\'m hiding')
                  //跳转
                  location.href="/";
              }
          })

          }else {
              this.$vux.alert.show({
                  title: '退出失败!',
                  content: '',
                  onShow () {
                  console.log('Plugin: I\'m showing')
              },
              onHide () {
                  console.log('Plugin: I\'m hiding')
              }
          })
          }
      });
      },
  }
}



