
import Search from 'vux/src/components/search/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
  components: {
    Search,
    Divider,
    Group,
    Datetime,
    XAddress,
    XButton,
  },
  methods: {
    resultClick (item) {
      window.alert('you click the result item: ' + JSON.stringify(item))
    },
    getResult (val) {
      this.results = val ? getResult(this.value) : []
    },
    onSubmit (val) {
      window.alert('on submit' + val)
    },
    onFocus () {
      console.log('on focus')
    },
    showPlugin3 () {
        console.log(111111111111)
        location.href="/?city_start="+this.city_start+"&city_end="+this.city_end+"&start_date="+this.start_date
    },
  },
  data () {
    return {
      addressData: ChinaAddressData,
      results: [],
      start_date: '',
      city_start: [],
      city_end: [],
    }
  }
}
function getResult (val) {
  let rs = []
  for (let i = 0; i < 8; i++) {
    rs.push({
      title: `${val} result: ${i + 1} `,
      other: i
    })
  }
  return rs
}



