
import Alert from 'vux/src/components/alert/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import value2name from 'vux/src/filters/value2name.js'


export default {
  components: {
    Group,
    Cell,
    XInput,
    Selector,
    PopupPicker,
    XAddress,
    Datetime,
    XNumber,
    XTextarea,
    XButton,
    Confirm,
      Marquee,
      MarqueeItem,
      Alert,
  },
  data () {
    return {
      addressData: ChinaAddressData,
      city_start: '',
      city_end: '',
      remark: '给车主留言',
      start_date: '',
      seat_num: 1,
      mobile:'',
      price:'',
      show: false,
        user:'',
        mobile:'',
        item:'',

    }
  },
    created(){
    this.fetchData()
    },
  methods: {
    getName (value) {
return value2name(value, ChinaAddressData)
},
        onCancel () {
         console.log('on cancel')
       },

           onHide () {
             console.log('on hide')
           },
           onShow () {
             console.log('on show')
           },
          fetchData(){

              var resource = this.$resource(APIURL+'order/getInfoByid', {
    //                    include: ''
              });
              resource.get({
                id: this.$route.params.id,
              }).then((response) => {
                  if(response.data.errcode==0){
                      console.log(response);
                      this.item = response.data.data;
                  }else {
              console.log(response);
                  }
              })
        },
      showPlugin3 () {
          console.log(cookie.get('laravel_token'))
          if(cookie.get('laravel_token')==undefined){
              this.alertPlugin('您还未登录！')
              return;
          }
          var that=this
          this.$vux.confirm.show({
              title: '确定预定?',
              content: '',
              hideOnBlur: true,
              onCancel () {},
              onConfirm (){
              that.ccyd()
            }
          })
      },
      alertPlugin (title) {
          this.$vux.alert.show({
              title: title,
              content: '',
              onShow () {
              console.log('Plugin: I\'m showing')
          },
          onHide () {
              console.log('Plugin: I\'m hiding now')
          }
      })
      },
      ccyd() {
          this.$http.post(APIURL+'orderPassanger/reserve', {
              order_id: this.$route.params.id,
              seat_num: this.seat_num,
              remark: this.remark,
          }).then(response => {
              if (response.data.errcode==0) {
              this.$vux.alert.show({
                  title: '预定成功!',
                  content: '',
                  onShow () {
                  console.log('Plugin: I\'m showing')
              },
              onHide () {
                  console.log('Plugin: I\'m hiding')
                  //跳转
                  location.href="/";
              }
          })

          }else {
              this.$vux.alert.show({
                  title: response.data.data,
                  content: '',
                  onShow () {
                  console.log('Plugin: I\'m showing')
              },
              onHide () {
                  console.log('Plugin: I\'m hiding')
              }
          })
          }
      });
      },

  }
}



