
import Alert from 'vux/src/components/alert/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'


export default {
  components: {
    Group,
    Cell,
    XInput,
    Selector,
    PopupPicker,
    XAddress,
    Datetime,
    XNumber,
    XTextarea,
    XButton,
    Confirm,
      Marquee,
      MarqueeItem,
      Alert,
  },
  data () {
    return {
      addressData: ChinaAddressData,
      city_start: ['福建省', '福州市', '仓山区'],
      city_end: ['福建省', '三明市', '大田县'],
      remark: '备注：',
      start_date: '',
      seat: 1,
      mobile:'',
      price:'100',
      show: false,
        user:'',
        mobile:'',

    }
  },
    created(){
    var date = new Date();
    var seperator1 = "-";
    var seperator2 = ":";
    var month = date.getMonth() + 1;
    var strDate = date.getDate();
    if (month >= 1 && month <= 9) {
        month = "0" + month;
    }
    if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
    }
    this.start_date = date.getFullYear() + seperator1 + month + seperator1 + strDate
            + " " + date.getHours() + seperator2 + date.getMinutes();

            // this.getCookie();
            if(cookie.get('phone_number')!='undefined'){
                this.mobile=cookie.get('phone_number')
            }
    },
  methods: {
        onCancel () {
         console.log('on cancel')
       },

           onHide () {
             console.log('on hide')
           },
           onShow () {
             console.log('on show')
           },
      czfb() {
          this.$http.post(APIURL+'order/save', {
              start_date: this.start_date,
              mobile: this.mobile,
              city_start: this.city_start,
              city_end: this.city_end,
              price: this.price,
              seat: this.seat,
              remark: this.remark,
          }).then(response => {
              if (response.data.errcode==0) {
              this.$vux.alert.show({
                  title: '发布成功!',
                  content: '',
                  onShow () {
                  console.log('Plugin: I\'m showing')
              },
              onHide () {
                  console.log('Plugin: I\'m hiding')
                  //跳转
                  location.href="/";
              }
          })

          }else {
              this.$vux.alert.show({
                  title: response.data.data,
                  content: '',
                  onShow () {
                  console.log('Plugin: I\'m showing')
              },
              onHide () {
                  console.log('Plugin: I\'m hiding')
              }
          })
          }
      });
      },
      showPlugin3 () {
          console.log(cookie.get('laravel_token'))
          if(cookie.get('laravel_token')==undefined){
              this.alertPlugin('您还未登录！')
              return;
          }
          var that=this
          this.$vux.confirm.show({
              title: '确定发布？',
              content: '',
              hideOnBlur: true,
              onCancel () {},
              onConfirm (){
              that.czfb()
            }
          })
      },
      alertPlugin (title) {
          this.$vux.alert.show({
              title: title,
              content: '',
              onShow () {
              console.log('Plugin: I\'m showing')
          },
          onHide () {
              console.log('Plugin: I\'m hiding now')
          }
      })
      },
      getCookie(){
          var laravel_token=cookie.get('laravel_token')
          if(laravel_token){
              this.fetchData()
          }else{
              console.log('您还未登录!');
          }
      },
      fetchData(){
          var resource = this.$resource(APIURL+'users/me', {
  //                    include: ''
          });
          resource.get({}).then((response) => {

              if(response.data.errcode==0){
                  console.log(response.data.data.phone_number);
                  this.mobile=response.data.data.phone_number
              }else {
                  console.log('出小差了');
              }
          });

      },

  }
}



