
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'


export default {
  components: {
    XInput,
    XButton,
    Group,
    Cell,
  },
  data () {
    return {
      password: '',
      phone_number: '',
      disabledValue: 'hello',
      debounceValue: '',
      maxValue: ''
    }
  },
  created(){
    if(cookie.get('phone_number')!='undefined'){
        this.phone_number=cookie.get('phone_number')
    }
  },
  methods: {
    change (val) {
      console.log(val)
    },
    onBlur (val) {
      console.log('on blur', val)
    },
    onFocus (val) {
      console.log('on focus', val)
    },
    login() {
        this.$http.post(APIURL+'users/login', {
          phone_number: this.phone_number,
          password: this.password,
        }).then(response => {
            if (response.data.errcode==0) {
              cookie.set('laravel_token', response.data.data.token);
              cookie.set('phone_number', this.phone_number);
              this.$vux.alert.show({
              title: '登录成功!',
              content: '',
              onShow () {
                console.log('Plugin: I\'m showing')

              },
              onHide () {
                console.log('Plugin: I\'m hiding')
                  //跳转
                  location.href="/"

              }
            })

            }else {
              this.$vux.alert.show({
              title: '账号密码有误!',
              content: '',
              onShow () {
                console.log('Plugin: I\'m showing')
              },
              onHide () {
                console.log('Plugin: I\'m hiding')
              }
            })
            }
      });
    },
  }
}



