
import cookie from 'vux/src/tools/cookie/index.js'
import Step from 'vux/src/components/step/step.vue'
import StepItem from 'vux/src/components/step/step-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHr from 'vux/src/components/x-hr/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'


export default {
  components: {
    Step,
    StepItem,
    XButton,
    XHr,
    Group,
    Cell,
    XInput,
    Selector,
    PopupPicker,
    XAddress,
    Datetime,
    XNumber
  },
  data () {
    return {
      step2: 0,
      name: '',
      phone_number: '',
      password: '',
      password2: '',
      plate_number: '',
      email: '',
      one:true,
      two:false,
      two_cz:false,
      three:false,
      four:false,
      type: ['乘客'],
      list: [['乘客', '车主']],
       valid1: false,
       valid2: false,
       valid3: false,
       valid4: false,
       valid5: false,
    }
  },
  methods: {
    change (val) {
  console.log(val)
},
getValid1 () {
  this.valid1 = this.$refs.input01.valid
  if(this.valid1===false){
    this.alertPlugin('姓名不能为空')
    return false
  }
  this.valid2 = this.$refs.input02.valid
  if(this.valid2===false){
    this.alertPlugin('手机号格式不对')
    return false
  }
  this.valid3 = this.$refs.input03.valid
  if(this.valid3===false){
    this.alertPlugin('邮箱格式不对')
    return false
  }
  this.valid4 = this.$refs.input04.valid
  if(this.valid4===false){
    this.alertPlugin('密码格式不对')
    return false
  }
  this.valid5 = this.$refs.input05.valid
  if(this.valid5===false){
    this.alertPlugin('密码格式不对')
    return false
  }
},
    nextStep () {
      this.step2 ++,
      console.log(this.step2)
      if(this.step2==1){
          //第2步
        if(this.type=='乘客'){
          this.one=false
          this.two=true
        }else if (this.type=='车主') {
          this.one=false
          this.two=true
          this.two_cz=true
        }
      }else if (this.step2==2) {
        //第3步
        var getValid1=this.getValid1()
        if(getValid1==false) {
          this.step2 --;
          return
        }else {
          this.one=false
          this.two=false
          this.two_cz=false

          this.$http.post(APIURL+'users/regedit', {
            type: this.type,
            name: this.name,
            phone_number: this.phone_number,
            password: this.password,
            email: this.email,
            plate_number: this.plate_number,
          }).then(response => {
              if (response.data.errcode==0) {
                  this.three=true
                  cookie.set('laravel_token', response.data.data.token)
                  this.$vux.alert.show({
                  title: '注册成功!',
                  content: '',
                  onShow () {
                    console.log('Plugin: I\'m showing')
                  },
                  onHide () {
                    console.log('Plugin: I\'m hiding')
                      location.href="/";

                  }
                })
              }else {
                this.$vux.alert.show({
                title: '注册失败!',
                content: response.data.data,
                onShow () {
                  console.log('Plugin: I\'m showing')
                },
                onHide () {
                  console.log('Plugin: I\'m hiding')
                }
              })
              this.four=true
              }
        });
        }

      }
    },
    alertPlugin (title) {
        this.$vux.alert.show({
            title: title,
            content: '',
            onShow () {
            console.log('Plugin: I\'m showing')
        },
        onHide () {
            console.log('Plugin: I\'m hiding now')
        }
    })
    },

  }
}



