       valid4: false,
       valid5: false,
    }
  },
  methods: {
    change (val) {
      console.log(val)
      },
      getValid1 () {
        this.valid3 = this.$refs.input03.valid
        if(this.valid3===false){
          alert('邮箱格式不对')
          return false
        }
        return true
      },
    nextStep () {
    var validate=this.getValid1()
    if(validate===true){
      this.password_email()
    }

    },
    password_email() {
        this.$http.post(APIURL+'users/password/email', {
          email: this.email,
        }).then(response => {
          console.log(response);
          if (response.data.errcode==0) {
            this.$vux.alert.show({
            title: '发送成功!',
            content: '',
            onShow () {
              console.log('Plugin: I\'m showing')
            },
            onHide () {
              console.log('Plugin: I\'m hiding')
                //跳转
                location.href="/#/account/Login";
            }
          })

          }else {
            this.$vux.alert.show({
            title: '发送失败!',
            content: '',
            onShow () {
              console.log('Plugin: I\'m showing')
            },
            onHide () {
              console.log('Plugin: I\'m hiding')
            }
          })
          }
      });
    },
  }
}



